import React, { PureComponent } from 'react'
import { connect } from 'react-redux';
// import { forEach, groupBy, isEmpty } from 'lodash';
import {
  Row, Col,
  // Button, Card, CardBody, CardHeader,
  // FormGroup, Input, Label,
} from 'reactstrap';
// import { Redirect, Route, Switch, Link } from 'react-router-dom';
// import { Nav, NavItem, Dropdown, DropdownItem, DropdownToggle, DropdownMenu, NavLink, Container } from 'reactstrap';
// import PropTypes from 'prop-types'
import { getDoanhThuCustomersThongKe, getDoanhThuTimeThongKe, getDonHangs } from 'reduxStore';
import { getTodayDetail } from 'services/SacMauMoiService';
// import { formatMoney } from '../../../services/SacMauMoiService';
import { YEAR } from '../../../constants/Constants';
import { DoanhThuCustomers } from './components/DoanhThuCustomers';
import { DoanhThuTime } from './components/DoanhThuTime';
import './ThongKe.scss'
// import { defaultProps } from 'recompose';

// const ThongKeTheoThoiGian = React.lazy(() => import('views/Pages/ThongKe/components/TheoThoiGian'));
// const ThongKeTheoCustomer = React.lazy(() => import('views/Pages/ThongKe/components/TheoCustomer'));

export class ThongKe extends PureComponent {
  static propTypes = {

  }

  static defaultProps = {
  }

  state = {
    doanhThuType: YEAR,
    doanhThu: 0,
  };

  toggle = () => {
    this.setState({
      dropdownOpen: !this.state.dropdownOpen
    });
  }

  componentDidMount() {
    const today = getTodayDetail();
    const year = today.year;
    this.props.getDoanhThuCustomersThongKe({ type: this.state.doanhThuType, year });
    this.props.getDoanhThuTimeThongKe({ year });
  }

  renderDoanhThuCustomers = () => {
    const { doanhThuCustomers } = this.props;
    return <DoanhThuCustomers doanhThuCustomers={doanhThuCustomers} />
  }

  renderDoanhThuTime = () => {
    const { doanhThuTime } = this.props;
    return <DoanhThuTime doanhThuTime={doanhThuTime} />
  }

  render() {
    return (
      <div className="mt-2">
        <Row>
          <Col sm="6">
            <h4>Doanh Thu Theo Khach Hang Trong Nam 2019</h4>
            {this.renderDoanhThuCustomers()}
          </Col>
          <Col sm="6">
          <h4>Doanh Thu Theo Thang Trong Nam 2019</h4>
            {this.renderDoanhThuTime()}
          </Col>
        </Row>
      </div>
    )
  }
}

const mapStateToProps = state => {
  const { doanhThuCustomers, doanhThuTime, donhangsThongKe, donhangs } = state.sacMauMoi;
  return { doanhThuCustomers, doanhThuTime, donhangsThongKe, donhangs }
}

export default connect(mapStateToProps, { getDonHangs, getDoanhThuCustomersThongKe, getDoanhThuTimeThongKe })(ThongKe)
