import React, { PureComponent } from 'react'
import { 
  // toArray, 
  isEmpty } from 'lodash'
import PropTypes from 'prop-types'
import { formatMoney } from 'services/SacMauMoiService';
import { DA_HUY } from '../../../../constants/Constants';

export class DoanhThuCustomers extends PureComponent {
  static propTypes = {
    doanhThuCustomers: PropTypes.object.isRequired,
  }

  state = {
    dropdownOpen: false,
    viewMode: 'viewCustomer',
  };

  chooseViewMode = ({ type }) => {
    this.setState({ viewMode: type })
  }

  renderDoanhThu = () => {
    const { doanhThuCustomers } = this.props;
    const doanhThuData = [];

    if (isEmpty(doanhThuCustomers)) return <div>Đang lấy dữ liệu</div>

    for (const customerName in doanhThuCustomers) {
      const donhangs = doanhThuCustomers[customerName];
      let doanhThu = 0;
      for (const donhang of donhangs) {
        if (donhang.trangThai === DA_HUY) {
          continue;
        }
        doanhThu += donhang.tongTien;
      }
      doanhThuData.push({ doanhThu, customerName })
    }
    doanhThuData.sort((x, y) => {
      return y.doanhThu - x.doanhThu;
    })

    return doanhThuData.map(doanhthu => {
      return (
        <div key={doanhthu.customerName} className="mt-2">
          <div>{doanhthu.customerName}</div>
          <div>{formatMoney(doanhthu.doanhThu)}</div>
        </div>
      )
    });
  }

  render() {

    return (
      <div>
        {this.renderDoanhThu()}
      </div>
    )
  }
}

export default DoanhThuCustomers
