import React, { Component } from 'react'
import { connect } from 'react-redux'
import { isEmpty } from 'lodash'
import PropTypes from 'prop-types'
import { formatMoney } from 'services/SacMauMoiService';
import { DA_HUY } from '../../../../constants/Constants';

export class DoanhThuTime extends Component {
  static propTypes = {
    doanhThuTime: PropTypes.object.isRequired,
  }

  state = {
    dropdownOpen: false,
    viewMode: 'viewTime',
  };

  chooseViewMode = ({ type }) => {
    this.setState({ viewMode: type })
  }

  renderDoanhThu = () => {
    const { doanhThuTime } = this.props;
    if (isEmpty(doanhThuTime)) return <div>Đang lấy dữ liệu</div>
    const result = [];
    for (const thang in doanhThuTime) {
      if (doanhThuTime.hasOwnProperty(thang)) {
        const donhangs = doanhThuTime[thang];
        let doanhThu = 0;
        for (const donhang of donhangs) {
          if (donhang.trangThai === DA_HUY) continue;
          doanhThu += donhang.tongTien;
        }
        result.push(<div className="mt-2" key={thang + doanhThu}>{`Doanh thu thang ${thang}: ${formatMoney(doanhThu)}`}</div>)
      }
    }
    return result;
  }

  render() {
    return (
      <div>
        {this.renderDoanhThu()}
      </div>
    )
  }
}

export default connect(null, null)(DoanhThuTime)
